@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:400,500&display=swap);
html,
body {
  height: 100%; }

body {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Roboto Mono", monospace, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #434343; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

@media (max-height: 600px) {
  html {
    height: auto; } }

@media (max-width: 450px) {
  body {
    font-size: 10px; } }

.panel {
  position: relative;
  color: #fff;
  background-color: #7d7d7f;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  width: 350px;
  height: 600px;
  padding: 1.5rem 3rem 2.5rem 3rem;
  border: 2px solid black;
  border-radius: 5px;
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(6, 1fr);
  grid-template-areas: "a a a" "a a a" "b b b" "b b b" "b b b" "b b b"; }

@media (max-width: 450px) {
  .panel {
    margin: 0;
    width: 70vmin;
    height: 75vh; } }

.panel__serial {
  font-size: 0.8rem;
  position: absolute;
  bottom: 10px;
  right: 15px;
  color: #434343; }

.panel__display {
  grid-area: a;
  display: grid;
  border-radius: 5px;
  color: #434343;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, 1fr);
  background-color: #7fffff;
  grid-template-areas: "c c c c" "d d d d" "d d d d"; }
  .panel__display span {
    grid-area: c;
    padding: 0.5rem;
    margin: 0; }
  .panel__display p {
    grid-area: d;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 2rem;
    overflow: hidden;
    margin: 0;
    font-size: 1.8rem;
    font-weight: 500; }

.panel__display_off {
  background-color: #47b2b2; }

@charset "UTF-8";
.panel__keypads {
  grid-area: b;
  display: grid;
  grid-gap: 1rem;
  margin: 0 2rem;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(4, 1fr);
  text-align: center; }
  .panel__keypads .panel__keypads__item {
    background-color: #63636e;
    color: #f3f3f3;
    touch-action: manipulation;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    font-size: 3em;
    font-weight: 500;
    border-radius: 3px;
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.4); }
  .panel__keypads :nth-child(2n):after {
    content: "";
    font-size: 0.7rem;
    font-weight: 600;
    color: #434343;
    position: absolute;
    bottom: 2px;
    right: 3px; }
  .panel__keypads :nth-child(2):after {
    content: "▲"; }
  .panel__keypads :nth-child(4):after {
    content: "◀"; }
  .panel__keypads :nth-child(6):after {
    content: "▶"; }
  .panel__keypads :nth-child(8):after {
    content: "▼"; }
  .panel__keypads :nth-child(10):after {
    content: "A"; }
  .panel__keypads :nth-child(12):after {
    content: "B"; }
  .panel__keypads .panel__keypads__item_active {
    box-shadow: none;
    color: #f1c40f; }

